
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.title {
  margin: 59px 0 0 0;
  text-align: center;
}

.search_from {
  width: 800px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 20px auto;
}
.input_fild {
  width: 100%;
}
.search_input,
.search_button {
  width: 20%;
  margin: 0 5px;
}
.search_input label {
  font-size: 14px;
}
.select_class,
.datepicker input,
.input_fild {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  color: #333;
  height: 40px;
  padding: 0 15px;
  border: 1px solid #333;
  border-radius: 4px;
  text-transform: uppercase;
  background: #fff;
  width: 100%;
}
.select_class {
  text-transform: none;
}
.datepicker input:focus,
.input_fild:focus {
  border-color: #3eaf7c;
}
.btn {
  height: 40px;
  background: #3eaf7c;
  border: 1px solid #3eaf7c;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
  border-radius: 4px;
  margin-top: 22px;
}
.btn:hover {
  background: #379e70;
}
.btn:disabled {
  background: #bbb;
  border-color: #bbb;
}
select,
input:focus,
button:focus {
  outline: none;
}
.error,
.loading,
.empty {
  text-align: center;
}

.offer-list {
  width: 1000px;
  margin: 0 auto;
}

.offer_item {
  border: 1px solid #bbb;
  margin-bottom: 15px;
  border-radius: 4px;
}
.offer_header {
  padding: 5px 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.carrier_logo {
  width: 250px;
}
.carrier_logo img {
  max-width: 200px;
}
.carrier_name {
  display: block;
  font-weight: bold;
}
.right_side {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.min_price {
  text-align: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: 10px;
}
.price {
  display: block;
  color: #333;
}
.currency {
  color: #3eaf7c;
  font-weight: bold;
}
.show_more {
  width: 50px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
}
.show_more:after {
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  border-right: 2px solid #333;
  border-bottom: 2px solid #333;
  -webkit-transform:rotate(45deg);
          transform:rotate(45deg);
  margin-top: -5px;
}
.show_more.active:after {
  margin-top: 0px;
  -webkit-transform:rotate(-135deg);
          transform:rotate(-135deg);
}
.show_more:hover:after {
  border-color: #3eaf7c;
}
.offer_detale {
  padding: 10px;
  border-top: 1px solid #bbb;
}

.matrix {
  text-align: center;
}
pre {
  max-width: 300px;
  text-align: left;
  margin: 0 auto;
}
